.dialogs {
    display: grid;
    grid-template-columns: 5fr 5fr;
    padding: 20px;
    background-color: #45484e;
}

.dialogItem {
    background-color: #45484e;
}

.dialog {
    background-color: rgba(255, 255, 255, 0.154);
    margin: auto;
    text-align: center;
    border-radius: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-bottom: 20px;
    width: 50%;
}

.messageUser {
    background-color: #2F3136;
    width: 100%;
    border-radius: 15px;
    text-align: right;
    padding: 10px 20px;
}

.messageUserWrap {
    width: max-content;
    border-radius: 15px;
    margin: auto;
    margin-right: 30px;
    background-color: #45484e;
}

.message {
    background-color: white;
    padding: 10px 20px;
    width: max-content;
    border-radius: 15px;
    color: black;
}

.messages {
    background-color: #45484e;
}

.link {
    background-color: rgba(255, 255, 255, 0);
    text-decoration: none;
}

.newPost {
    float: right;
    background-color: #45484e;
}